.PopUpHeaderSetting_checkbox__AU28C {
	position: absolute;
	z-index: -1;
	opacity: 0;
	margin: 10px 0 0 20px;
}
.PopUpHeaderSetting_checkbox__AU28C + label {
	position: relative;
	padding: 0 0 0 60px;
	cursor: pointer;
}
.PopUpHeaderSetting_checkbox__AU28C + label:before {
	content: '';
	position: absolute;
	top: -4px;
	left: 0;
	width: 50px;
	height: 26px;
	border-radius: 13px;
	background: #CDD1DA;
	box-shadow: inset 0 2px 3px rgba(0,0,0,.2);
	transition: .2s;
}
.PopUpHeaderSetting_checkbox__AU28C + label:after {
	content: '';
	position: absolute;
	top: -2px;
	left: 2px;
	width: 22px;
	height: 22px;
	border-radius: 10px;
	background: var(--light_gray);
	box-shadow: 0 2px 5px rgba(0,0,0,.3);
	transition: .2s;
}
.PopUpHeaderSetting_checkbox__AU28C:checked + label:before {
	background: #73776f;
}
.PopUpHeaderSetting_checkbox__AU28C:checked + label:after {
	left: 26px;
}
.PopUpHeaderSetting_checkbox__AU28C:focus + label:before {
	box-shadow: inset 0 2px 3px rgba(0,0,0,.2), 0 0 0 3px rgba(197, 197, 191, 0.7);
}
